<template>
  <div id="tablet">
    <div class="queryBox">
      <div class="queryBtns">
        <Button type="success" size="small" icon="md-add" @click.stop="addModal = true" class="btn-add">新建</Button>
      </div>
    </div>
    <div class="content_box">
      <div>
        <Table :loading="loading" border stripe :columns="columns1" :data="data1" highlight-row :max-height="tableHeight"></Table>
      </div>
    </div>
    <div class="add_box" :class="{addActive:addActive}">
      <WeightDetail ref="WeightDetail" :name="detailname" @signOut="signOut"></WeightDetail>
    </div>
    <!-- 新增 -->
    <Modal
      v-model="addModal"
      title="新增">
      <div>
        <Form ref="addForm" :model="AddData" :label-width="60" style="padding:0 20%;">
          <FormItem prop="name" label="名称：">
            <Input v-model.trim="AddData.name" placeholder="请输入名称" size="default"/>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <i-button type="text" @click="addModal = false" class="mr10">取消</i-button>
        <Button type="primary" @click="addNumber">确定</Button>
      </div>
    </Modal>
    <!-- 编辑 -->
    <Modal
      v-model="uploadModal"
      title="编辑">
      <div>
        <Form ref="addForm" :model="uploadData" :label-width="60" style="padding:0 20%;">
          <FormItem prop="name" label="名称：">
            <Input v-model.trim="uploadData.name" placeholder="请输入名称" size="default"/>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <i-button type="text" @click="uploadModal = false" class="mr10">取消</i-button>
        <Button type="primary" @click="updateNume">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import WeightDetail from './WeightDetail'

import { getNumberNames2, updateNume2, deleteNumber2, addNumber2, defaultNumber2 } from '@/api/weightLibrary'
export default {
  name: 'ActualTablet',
  data () {
    return {
      /* 查询框查询条件 */
      queryData: {},
      /* 表格高度 */
      tableHeight: window.innerHeight - 312,
      /* 表头 */
      columns1: [
        /* {
          title: '序号',
          key: 'jzKh',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        }, */
        {
          title: '是否默认',
          key: 'jzKh',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('Icon', {
              style: {
                fontSize: '20px',
                color: 'rgb(255, 153, 0)'
              },
              props: {
                type: params.row.sign === 0 ? 'ios-star-outline' : 'ios-star'
              },
              on: {
                'click': () => {
                  this.changeDefault(params.row.name)
                }
              }
            })
          }
        },
        {
          title: '权重模板',
          key: 'name',
          align: 'center',
          minWidth: 300
        },
        {
          title: '操作',
          key: 'isBs',
          align: 'center',
          width: 250,
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-eye'
                },
                style: {
                  marginRight: '10px'
                },
                on: {
                  click: () => {
                    this.detailname = params.row.name
                    this.addActive = true
                  }
                }
              }, '查看'),
              h('Button', {
                props: {
                  type: 'warning',
                  size: 'small',
                  icon: 'md-create'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.oldname = params.row.name
                    this.uploadData.name = params.row.name
                    this.uploadModal = true
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该项目么？',
                      onOk: () => {
                        this.deleteNumber(params.row.name)
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      /* 表格数据 */
      data1: [],
      /* 页码 */
      pageNum: 1,
      /* 每页条数 */
      pageSize: Math.floor((window.innerHeight - 312) / 48),
      /* 总条数 */
      total: 0,
      /* 加载 */
      loading: false,
      /* 新建框状态 */
      addActive: false,
      /* 查看数据的id */
      seeId: 0,
      fileUrl: '',
      /* 新增修改 */
      AddData: {},
      /* 新增modal */
      addModal: false,
      /* 新增修改 */
      uploadData: {},
      /* 新增modal */
      uploadModal: false,
      /* 修改前的名称 */
      oldname: '',
      /* 查看详情的名称 */
      detailname: ''
    }
  },
  components: {
    WeightDetail
  },
  computed: {
  },
  created () {
    this.getTableData()
  },
  mounted () {
    // 窗口改变时图表自适应
    window.addEventListener('resize', () => {
      this.tableHeight = Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48)
      this.getTableData()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableData()
    },
    /* 获取项目列表 */
    getTableData () {
      getNumberNames2({
        data: {
        }
      }).then((res) => {
        this.data1 = res.data.data
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 新建 */
    addNumber () {
      addNumber2({
        data: {
          name: this.AddData.name
        }
      }).then(() => {
        this.$Notice.success({ title: '创建成功' })
        this.addModal = false
        this.getTableData()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 编辑 */
    updateNume (name) {
      updateNume2({
        data: {
          name: this.oldname,
          data: this.uploadData.name
        }
      }).then(() => {
        this.$Notice.success({ title: '编辑成功' })
        this.uploadModal = false
        this.getTableData()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 删除 */
    deleteNumber (name) {
      deleteNumber2({
        data: {
          name: name
        }
      }).then(() => {
        this.$Notice.success({ title: '删除成功' })
        this.getTableData()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 修改默认 */
    changeDefault (name) {
      defaultNumber2({
        data: {
          name: name
        }
      }).then(() => {
        this.getTableData()
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 详情页面退出 */
    signOut (bool) {
      this.addActive = bool
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        position: absolute;
        bottom: 0;
        right: 0;
        width: 100%;
        padding: 15px;
        text-align: right
      }
    }
    .btn-add {
      background-color: #01c296;
      border-color: #01c296;
      &:hover, &:focus {
        background-color: #05ab85;
        border-color: #05ab85;
      }
    }
    .exportBtn {
      background: #2d579b;
      border-color: #2d579b;
      margin-right: 10px;
      &:hover {
        background: #1a3a6f;
        border-color: #1a3a6f;
      }
    }
    .btn-download {
      background-color: #1ccf20;
      border-color: #1ccf20;
      &:hover, &:focus {
        background-color: #13bb17;
        border-color: #13bb17;
      }
    }
    .add_box{
      height: 100%;
      width: 100%;
      position: absolute;
      right: -120%;
      top:0;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      border-radius: 0 0 5px 5px;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right:0;
    }
  }
</style>
