<template>
  <div class="weightDetail">
    <div class="queryBox">
      <div class="search-title" style="text-align: left;font-size: 14px;line-height: 50px">
        修改权重 - {{name}}
      </div>
    </div>
    <div style="height: calc(100% - 150px);overflow: auto;margin: 0 30px">
      <Form v-model="numformItem" :label-width="140" inline>
        <div v-for="(ite, ind) in weightClass" :key="ind">
          <!--<h5 style="margin: 5px 25px;padding: 5px 8px;font-size: 13px;width: 150px">{{ite}}：</h5>-->
          <Divider orientation="left">
            <Icon type="md-arrow-dropright" />{{ite}}
          </Divider>
          <FormItem v-for="(item, index) in numData.filter(res => { return res.parentName === ite })" :key="index" :label="item.data">
            <Input v-model="numformItem[item.id]" :placeholder="item.data"></Input>
          </FormItem>
        </div>
      </Form>
    </div>
    <p style="text-align:right;margin:10px 15px;padding:10px 5px;border-top: 1px solid #d3d3d3;">
      <Button type="primary" @click="updateNumber" style="margin-right: 10px">保存</Button>
      <Button type="primary" @click="$emit('signOut', false)">返回</Button>
    </p>
  </div>
</template>
<script>
import { getNumberByName2, updateNumber2 } from '@/api/weightLibrary'
import { distinct } from '@/utils/common'
export default {
  name: 'weightDetail',
  data () {
    return {
      numData: [],
      numformItem: {}
    }
  },
  computed: {
    weightClass () {
      let arr = []
      for (let item of this.numData) {
        arr.push(item.parentName)
      }
      arr = distinct(arr)
      return arr
    }
  },
  props: {
    name: {}
  },
  components: {
  },
  watch: {
    name (name) {
      this.getNumberByName(name)
    }
  },
  mounted () {
    this.getNumberByName(this.name)
  },
  methods: {
    /* 获取详情信息 */
    getNumberByName (name) {
      getNumberByName2({
        data: {
          name: name
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.numData = res.data.data
          for (let item of this.numData) {
            this.numformItem[item.id] = item.number
          }
        } else {
          /* 错误信息弹框 */
          this.$Notice.error({ title: '获取失败', desc: res.data.msg })
        }
      })
    },
    /* 保存 */
    updateNumber (name) {
      updateNumber2({
        data: {
          json: this.numformItem
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '保存成功' })
        } else {
          /* 错误信息弹框 */
          this.$Notice.error({ title: '保存失败', desc: res.data.msg })
        }
      })
    }
  }
}
</script>
<style scoped lang="scss">
.weightDetail{
  text-align: left;
  height: 100%;
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    .queryBtns {
      float: right;
      margin-top: 14px;
    }
    .search-title {
      &:before {
        content: '';
        display: inline-block;
        width: 18px;
        height: 18px;
        background: url("../../assets/redLine/sprite.png") 0 -63px no-repeat;
        background-size: auto;
        vertical-align: middle;
        margin-right: 8px;
      }
    }
  }
}
</style>
